.bannerabout {
    position: relative;
    height: 223px;
    background-size: cover;
    background-position: center;
    border-radius: 30px;
    margin: 20px;
    overflow: hidden;
}

.bannerabout__overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: center;
    align-items: center;
}

.bannerabout__text {
    color: white;
    font-size: 48px;
    font-weight: bold;
    text-align: center;
}